body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    border: 4px rgb(99, 99, 99) solid;
    border-radius: 2px;
    width: 300px;
    height: 300px;
    margin: 10px, 10px, 10px, 10px;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    transition: all .2s ease-in-out;

  }
  
  .card > .img-container {
    height: 300px;
    width: 300px;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .card > .img-container > img {
    height: 300px;
    width: 300px;
  }
  
  .card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'Bungee', cursive;
  }
  
  a {
    color: #fed420;
  }
  
  a:hover {
    color: #fed420;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    /* text-align: center; */
  }
  
  ul {
    /* padding-left: 0px; */
    display: flex;
    align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }
  
.wrapper {
    background: rgb(175, 175, 175);
    background-image: url('http://www.techpresentations.org/myphoto/f/63/630834_harry-potter-houses-wallpaper.jpg');
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    padding-bottom: 50px;
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 3rem;
    font-family: lumos;
    color: rgb(236, 240, 241);
    margin-bottom: 50px;
    padding-left: 3%;
    padding-right: 3%;  
  }
